import { generateRemoteUrl, generateUrl } from '@nextcloud/router';
import { getCurrentUser } from '@nextcloud/auth';
import UserConfig from '../mixins/UserConfig';
export default {
  name: 'File',
  mixins: [UserConfig],
  inheritAttrs: false,
  props: {
    item: {
      type: Object,
      required: true
    }
  },

  data() {
    return {
      loaded: false,
      error: false
    };
  },

  computed: {
    davPath() {
      return generateRemoteUrl("dav/files") + this.item.injected.filename;
    },

    ariaUuid() {
      return "image-".concat(this.item.injected.fileid);
    },

    ariaLabel() {
      return t('photos', 'Open the full size "{name}" image', {
        name: this.item.injected.basename
      });
    },

    isImage() {
      return this.item.injected.mime.startsWith('image');
    },

    src() {
      return generateUrl("/core/preview?fileId=".concat(this.item.injected.fileid, "&x=", 256, "&y=", 256, "&a=").concat(!this.croppedLayout, "&v=").concat(this.item.injected.etag));
    }

  },

  beforeDestroy() {
    // cancel any pending load
    this.$refs.src = '';
  },

  methods: {
    openViewer() {
      OCA.Viewer.open({
        path: this.item.injected.filename,
        list: this.item.injected.list,
        loadMore: this.item.injected.loadMore ? async () => await this.item.injected.loadMore(true) : () => [],
        canLoop: this.item.injected.canLoop
      });
    },

    /** When the image is fully loaded by browser we remove the placeholder */
    onLoad() {
      this.loaded = true;
    },

    onError() {
      this.error = true;
    }

  }
};